import { getDataLocalStorage } from '../api/localStorageFavcCocktails';
import { FAV_COCKTAILS } from '../utils/constans';

export function markupInfoCoctail(obj) {
  const keysIngredients = Object.keys(obj).filter(el =>
    el.includes('strIngredient')
  );
  const favoriteCocktails = getDataLocalStorage(FAV_COCKTAILS);
  const { idDrink, strDrink, strDrinkThumb, strInstructions } = obj;
  const foundEl = favoriteCocktails.find(el => el.idDrink === idDrink);

  return `<div class="product-info modal__infoCocktail">
  <button class="modal-close">CLOSE modal</button>
  <button class="products__btn--add" data-id="${idDrink}" data-action="${
    !foundEl ? 'add' : 'remove'
  }">
   ${!foundEl ? 'add to' : 'remove'}
  </button>
  <div class="product-info__container">
    <img
      src="${strDrinkThumb}"
      width="100"
      alt="${strDrink}"
      class="product-info__img"
    />
    <h4 class="product-info__title">${strDrink}</h4>

    <ul class="product-info__list--ingr">
      ${keysIngredients
        .map(key =>
          obj[key]
            ? `
      <li class="product-info__item--ing">
        <span class="product-info__item--span" > &sext; </span>
        <a class="product-info__item--title" href="#">${obj[key]}</a>
      </li>
      `
            : null
        )
        .join('')}
      <li class="product-info__item--ing"></li>
    </ul>
  </div>
  <p>instruction</p>
  <p>${strInstructions}</p>
</div>`;
}

