import { getDataLocalStorage } from '../api/localStorageFavcCocktails';
import { FAV_COCKTAILS } from '../utils/constans';

export const markupSearchingCocktails = arrDrinks => {
  const favoriteCocktails = getDataLocalStorage(FAV_COCKTAILS);
  return arrDrinks
    .map(({ idDrink, strDrink, strDrinkThumb }) => {
      const foundEl = favoriteCocktails.find(el => el.idDrink === idDrink);
      const btnAdd = `<button class="products__btn--add" data-id="${idDrink}"  data-action="${
        foundEl ? 'remove' : 'add'
      }"> ${!foundEl ? 'add to' : 'remove'}  </button>`;

      return `<li  class="products__item" data-id="${idDrink}" >
          <p class="products__name">${strDrink}</p>
          <img src="${strDrinkThumb}" alt=${strDrink} class="products__img">
              <div class="products__btns" data-id=${idDrink} >
      <button class="products__btn--info" >Learn more</button>
      ${btnAdd}
    </div>
        </li>`;
    })
    .join('');
};
