import * as basicLightbox from 'basiclightbox';

import { fetchInfoProductById } from '../api/api';
import { markupInfoCoctail } from '../templates/markupInfoCoctail';
import { onReject } from '../utils/utils';
import { getInfoIngredient } from './getInfoIngredient';
import { getProductsInLocalStorage } from '../localStorage/getProductsInLocalStorage';
import { refs } from '../refs/refs';
// import { FAV_COCKTAILS } from '../utils/constans';

export async function getInfoCocktail(e) {
  const el = e.target.closest('.products__btn--info');
  const elInStorage = e.target.closest('.products__btn--add');
  if (!el && !elInStorage) return;

  if (elInStorage) {
    const coctailId = elInStorage.parentNode.dataset.id;
    const actionEl = elInStorage.dataset.action;
    if (actionEl === 'add') {
      elInStorage.dataset.action = 'remove';
      elInStorage.textContent = 'remove';
    } else {
      // console.log('getInfoCocktail', window.location.pathname);
      if (
        window.location.pathname === '/partials/favorite-cocktails.html' ||
        window.location.pathname ===
          '/js-team-project/partials/favorite-cocktails.html'
      ) {
        // /js-team-project/partials/favorite-cocktails.html
        // /js-team-project/index.html
        console.log('видаляю з розмітки');
        e.target.closest('.products__item').remove();
      }
      elInStorage.dataset.action = 'add';
      elInStorage.textContent = 'add to';
    }
    getProductsInLocalStorage(coctailId);
    return;
  }
  const coctailId = el.parentNode.dataset.id;
  try {
    const result = await fetchInfoProductById(coctailId);
    onResolve(...result.drinks);
  } catch (error) {
    onReject(error);
  }
}

function onResolve(objProduct) {
  if (!objProduct) return;

  const instance = basicLightbox.create(
    `
      <div class ="modal  modal__fav--coctails"> ${markupInfoCoctail(
        objProduct
      )} </div>
    `,
    {
      onShow: instance => {
        const elementsInModal = instance.element();

        elementsInModal
          .querySelector('.modal__fav--coctails')
          .addEventListener('click', getInfoIngredient);
        elementsInModal
          .querySelector('.modal-close')
          .addEventListener('click', instance.close);
        if (
          window.location.pathname === '/partials/favorite-cocktails.html' ||
          window.location.pathname ===
            '/js-team-project/partials/favorite-cocktails.html'
        ) {
          elementsInModal
            .querySelector('[data-action="remove"]')
            .addEventListener('click', instance.close);
        }
      },
    }
  );
  instance.show();
}
