import { getDataLocalStorage } from '../api/localStorageFavcCocktails';
import { FAV_INGREDIENTS } from '../utils/constans';

export function markupInfoIngredient(obj) {
  const favoriteIngredients = getDataLocalStorage(FAV_INGREDIENTS);
  const { idIngredient, strIngredient, strType, strDescription } = obj;
  const foundEl = favoriteIngredients.find(
    el => el.idIngredient === idIngredient
  );
  return `<button class="close-modal-ingr-info">close</button>
<button class="favorite-ingred-btn"  data-id="${idIngredient}" data-action="${
    foundEl ? 'remove' : 'add'
  }"    >${foundEl ? 'remove' : 'add'}</button>
<h3>${strIngredient}</h3>
<p>${strType}</p>
<p>${strDescription}</p>`;
}
